.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  display: flex;
  flex-direction: column;
  background-color: #282c34;
  justify-content: center;
  align-items: center;
}

form {
  width: 480px;
  height: 100px;
  border-radius: 10px;
  background-color: white;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center
}

.image-display {
  display: flex;
  flex-direction: row;
  margin: 20px;
  justify-content: space-around;
  align-items: center;
  color: white;
}
img{
  max-width: 300px;
  max-height: 300px;
}